<template>
  <section id="gallery">
    <div class="gallery-content">
      <!-- Text Content on the Left -->
      <div class="text-content">
        <h2>Photo Gallery</h2>
        <p>Explore our collection of high-quality vehicles, tools, and services.</p>
        <p>Click through the images to see the full gallery.</p>
      </div>

      <!-- Gallery Carousel Container on the Right -->
      <div class="carousel-container">
        <div class="carousel">
          <!-- Apply transition for smooth fade -->
          <transition name="fade" mode="out-in">
            <!-- Dynamically change image based on the activeGalleryIndex -->
            <img 
              :key="activeGalleryIndex"
              :src="galleryImages[activeGalleryIndex]" 
              :alt="'Gallery Image ' + (activeGalleryIndex + 1)" 
            />
          </transition>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: 'PhotoGallery',
  data() {
    return {
      galleryImages: [
        require('../assets/gallery1.jpg'),
        require('../assets/gallery2.jpg'),
        require('../assets/gallery3.jpg')
      ],
      activeGalleryIndex: 0
    };
  },
  mounted() {
    setInterval(() => {
      // Change the active index to the next image in the array
      this.activeGalleryIndex = (this.activeGalleryIndex + 1) % this.galleryImages.length;
    }, 3000); // Change image every 3 seconds
  }
};
</script>

<style scoped>
/* Gallery Section Styling */
#gallery {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
}

.gallery-content {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.text-content {
  flex: 1;
  padding-right: 20px;
}

.text-content h2 {
  font-size: 2.5em;
  margin-bottom: 20px;
  color: #333;
}

.text-content p {
  font-size: 1.2em;
  color: #555;
  line-height: 1.5;
  margin-bottom: 15px;
}

/* Carousel container styling */
.carousel-container {
  position: relative;
  width: 50%; /* Take up half of the space */
  height: 400px;
  margin: 0 20px;
  overflow: hidden;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /* Optional: Add a shadow */
}

/* Image styling for the carousel */
.carousel img {
  width: 100%;
  height: 100%;
  object-fit: cover; /* Ensures the image covers the container without stretching */
}

/* Fade Transition Effect */
.fade-enter-active, .fade-leave-active {
  transition: opacity 1s ease-in-out;
}

.fade-enter, .fade-leave-to {
  opacity: 0; /* Set opacity to 0 when entering or leaving */
}

/* Optional: Responsive Styles */
@media (max-width: 768px) {
  .gallery-content {
    flex-direction: column;
    text-align: center;
  }

  .text-content {
    width: 100%;
    margin-bottom: 20px;
    padding-right: 0;
  }

  .carousel-container {
    width: 100%;
    height: 250px;
  }
}
</style>
